enter\"\n                         label=\"管理费\"\n                         prop=\"total_fee\"></el-table-column>\n\n        <!-- <el-table-column align=\"center\"\n                         label=\"补交押金\"\n                         prop=\"car_fee\"></el-table-column> -->\n        <el-table-column align=\"center\"\n                         label=\"提交时间\"\n                         prop=\"create_time\"></el-table-column>\n        <el-table-column align=\"center\"\n                         label=\"操作\">\n          <template slot-scope=\"scope\">\n            <el-button type=\"text\"\n                       @click=\"visibleDetails=true,details(scope.row.id)\">详情</el-button>\n          </template>\n        </el-table-column>\n      </el-table>\n      <el-dialog title=\"详情\"\n                 :visible.sync=\"visibleDetails\"\n                 center>\n        <div class=\"margin\">\n          <span>补交押金 : </span>\n          <span>{{DetailsList.car_fee}}</span>\n        </div>\n        <div class=\"margin\">\n          <span>支付时间 : </span>\n          <span>{{DetailsList.pay_time}}</span>\n        </div>\n        <div>\n          <div v-for='(item,index) in DetailsList.img'\n               :key=\"index\"\n               class=\"images\">\n            <div>\n              <span>\n                <img :src=\"item.Z_IDcard\"\n                     ref=\"images\" />\n              </span>\n              <p>身份证(正)</p>\n            </div>\n            <div>\n              <span>\n                <img :src=\"item.F_IDcard\"\n                     ref=\"images\" />\n              </span>\n              <p>身份证(反)</p>\n            </div>\n            <div>\n              <span>\n                <img :src=\"item.driving\"\n                     ref=\"images\" />\n              </span>\n              <p>驾驶证</p>\n            </div>\n            <div>\n              <span>\n                <img :src=\"item.credit\"\n                     ref=\"images\" />\n              </span>\n              <p>征信查询授权</p>\n            </div>\n            <div>\n              <span>\n                <img :src=\"item.warranty\"\n                     ref=\"images\" />\n              </span>\n              <p>授权照片</p>\n            </div>\n          </div>\n        </div>\n\n      </el-dialog>\n      <!-- 分页 -->\n      <div class=\"page_center\"\n           v-show=\"pageCount&&pageCount>1\">\n\n        <paging :page-count=\"pageCount\"\n                :page=\"page\"\n                @index=\"paging\"></paging>\n      </div>\n\n    </div>\n  </div>\n</template>\n<script type=\"text/ecmascript-6\">\nimport Viewer from '@/utils/Viewer'\nexport default {\n  data () {\n    return {\n      list: [],\n      token: window.sessionStorage.getItem('bbytoken'),\n      //分页数据\n      page: 1, //显示的页码数\n\n      authList: [],\n      curId: 0,\n      threeAuthList: [],\n      seCurId: 0,\n      thCurId: 0,\n      visibleDetails: false,\n      DetailsList: {}\n    }\n  },\n  methods: {\n\n    paging (e) {\n      this.page = e;\n      this.init()\n    },\n\n    //获取申请列表数据\n    init () {\n      this.$axios.post('admin/ShopList/serveFeeCar', {\n        token: this.token,\n        page: this.page,\n        handle: 1\n      })\n        .then(res => {\n          if (res.data.code == 1) {\n            this.list = res.data.data.data;\n            this.pageCount = res.data.data.rows;\n          } else {\n            layer.msg(res.data.msg)\n          }\n        })\n        .catch(err => console.log(err))\n    },\n    async details (id) {  //查看详情\n      try {\n        const res = await this.$axios.post('admin/ShopList/detailCar', { id: id, token: this.token })\n        this.DetailsList = res.data.data\n        this.$nextTick(() => {\n          Viewer(this.$refs.images)\n        })\n      } catch (err) {\n        throw (err)\n      }\n    }\n\n  },\n\n  mounted () {\n    this.init()\n    var id = this.$route.query.id;\n    this.curId = id;\n    this.$axios.post('admin/Auth/erAuth', {\n      token: window.sessionStorage.getItem('bbytoken'),\n      id: id\n    })\n      .then(res => {\n\n        if (res.data.code == 1) {\n          var arr = res.data.data;\n          for (var i = 0; i < arr.length; i++) {\n            if (arr[i].son) {\n              if (arr[i].name == '服务车审核') {\n                this.seCurId = arr[i].id;\n                this.threeAuthList = arr[i].son;\n\n              }\n              for (var j = 0; j < arr[i].son.length; j++) {\n                if (arr[i].action != arr[i].son[j].action) {\n                  arr[i].action = arr[i].son[0].action;\n                }\n                if (arr[i].son[j].name == '已审核' && arr[i].name == '服务车审核') {\n                  this.thCurId = arr[i].son[j].id;\n                }\n              }\n            }\n          }\n          this.authList = arr;\n        } else {\n          this.$alert(res.data.msg, '提示', {\n            type: 'error'\n          });\n        }\n      })\n      .catch(err => {\n        alert(err);\n      })\n  }\n\n}\n</script>\n<style scoped>\n.margin {\n  margin: 10px 0;\n}\n.images > div {\n  display: inline-block;\n  width: 90px;\n  height: 90px;\n  margin: 0 10px 30px 0;\n  text-align: center;\n}\n.images > div > span {\n  display: inline-block;\n  width: 100%;\n  height: 100%;\n  background-color: #ccc;\n}\n.images img {\n  width: 100%;\n  height: 100%;\n}\n</style>"],"sourceRoot":""}]);

